import React, { useState } from 'react';
import { ReactComponent as GetInvolvedIcon } from '../../assets/images/mobileNavbar/getInvolved.svg';
import { ReactComponent as LoginIcon } from '../../assets/images/mobileNavbar/login.svg';
import { ReactComponent as MeetUsIcon } from '../../assets/images/mobileNavbar/meetUs.svg';
import { ReactComponent as OurPgmIcon } from '../../assets/images/mobileNavbar/ourPgm.svg';
import NavBrand from './NavBrand';
import NavLink from './NavLink';
import NavLinkExpandable from './NavLinkExpandable';
import NavMenu from './NavMenu';

const Navbar = () => {
  const [navbarOpen, setNavbarOpen] = useState(false);
  const toggle = () => setNavbarOpen(!navbarOpen);

  const menuToggle = () => (navbarOpen ? setNavbarOpen(!navbarOpen) : null);

  return (
    <div
      className={
        'navbar bottom-0 lg:bottom-auto flex w-full justify-center bg-white fixed backdrop-blur-18 shadow-navbar nav-fallback lg:bg-opacity-50' +
        (navbarOpen ? ' rounded-t-navbar bg-opacity-100' : ' bg-opacity-50')
      }
    >
      <div className="flex items-center flex-wrap w-full sm:w-5/6 lg:w-4/6">
        <NavBrand toggle={menuToggle}>Hack Your Own</NavBrand>
        <NavMenu open={navbarOpen} toggle={toggle} />
        <div
          className={
            'w-full lg:space-x-9 lg:flex lg:items-center lg:w-auto' +
            (navbarOpen ? '' : ' hidden')
          }
        >
          <NavLink
            to="/program"
            toggle={toggle}
            icon={<OurPgmIcon />}
            text="Our Program"
          />
          <NavLink
            to="/get-involved"
            toggle={toggle}
            icon={<GetInvolvedIcon />}
            text="Get Involved"
          />
          <NavLinkExpandable
            to="/meet-us"
            toggle={toggle}
            icon={<MeetUsIcon />}
            text="Meet Us"
            submenu={[
              { name: 'Students', link: '/meet-us/#' },
              { name: 'Mentors', link: '/meet-us/#' },
              { name: 'Staff', link: '/meet-us/#' },
            ]}
          />
          <NavLink
            to="/login"
            toggle={toggle}
            icon={<LoginIcon />}
            text="Login"
          />
        </div>
      </div>
    </div>
  );
};

export default Navbar;
